import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import { Apple } from './components/apple'
import { GithubIcon } from './components/github-icon'
import { Footer } from './components/footer'
import logo from '../assets/icon_256x256.png'
import gif from '../assets/rainbow.gif'
import ny from '../assets/NY.jpg'
import favicon from '../assets/favicon.png'
import './index.css'
import './styles.css'

const projectGitHubURL = 'https://github.com/alessioforte/transee'

const Header = () => (
  <div id='header'>
    <div className='container'>
        <div
          onClick={function() { window.open(projectGitHubURL) }}
        >
          <GithubIcon />
        </div>
    </div>
  </div>
)

const PageHeader = () => (
  <div id='pageHeader'>

      <img
        id='logo'
        src={logo}
        alt='logo'
      />

      <div id='description'>

        <h3>Transee</h3>
        Simple and usefull tool for quick translation

        <div id='downloads'>
          <Apple />
        </div>

      </div>

  </div>
)

const TemplateWrapper = ({ children }) => (
  <div>
    <Helmet
      title="Transee"
      meta={[
        { name: 'description', content: 'Quick reference to Google translate desktop app' },
        { name: 'keywords', content: ' transee, goolge, translate, desktop, app' },
      ]}
    >
      <link rel="shortcut icon" href={favicon}/>
    </Helmet>
    <div>

      <Header />

      <div id='NYtop'>
        <PageHeader />
      </div>

      <div id='NYcenter'>
        <img
          id='gif'
          src={gif}
          alt='gif'
        />
      </div>
      <Footer />
    </div>
  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper



